.Header{

    width: 100vw;
    height: 48px;

    background-color: white;


    display: flex;
    align-items: center;

    position:fixed;
}


.logo{
    margin-left: 230px;
    cursor: pointer;
}

.poisk{
    width: 200px;
    height: 32px;
    border: none;
    border-radius: 10px;
    padding-left: 30px;

    background: #edeef0;

    outline: none;

    margin-left: 30px;

    cursor: text;
}

.poisk::placeholder{
    font-weight: 550;
    opacity: .5;
}


.elem_l{
    width: 44px;
    height: 100%;
    align-items: center;
    justify-content: center;
    display: flex;
    margin-left: 10px;
    cursor: pointer;

}
.elem_l:hover{
    background: #EDEEF0;
    
}


.elem_l_1{
    width: 44px;
    height: 100%;
    align-items: center;
    justify-content: center;
    display: flex;
    margin-left: 320px;
    cursor: pointer;

}
.elem_l_1:hover{
    background: #EDEEF0;
    
}

.avatar{
    width: 32px;
    height: 32px;
    background: crimson;
    border-radius: 50%;
    
}